
.cover {
  height: 100vh;
  width: 100vw;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.animation-tile, .animation-underlay {
  width: 65%;
  height: 65%;
  margin: auto;
  position: absolute;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.animation-underlay {
  background-color: lightslategray;
}
.animation-tile {
  opacity: .3;
}
.clickable-overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  cursor: crosshair;
}
.cover-text {
  position: absolute;
  width: 61%;
  color: #fff;
  text-align: center;
  font-size: 42pt;
  font-weight: bold;
  letter-spacing: .5vw;
}
@media (max-width: 500px) and (orientation: portrait) {
.cover-text {
    font-size: 11vmin;
}
}



